/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2006-11-07-blogher-presentsworld-domination-via-collaboration/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"http://www.jorydesjardins.com/\">Jory Des Jardins</a>, Co-founder, BlogHer LLC\n<a href=\"http://www.caterina.net/\">Caterina Fake</a>, Co-founder, Flickr\n<a href=\"http://www.buzzle.com/authors.asp?author=1692\">Jessica Hardwick</a>, Founder and CEO, SwapThing.com - people exchange whatever. Uses social collateral.\n<a href=\"http://surfette.typepad.com/\">Lisa Stone</a>, Co-founder, BlogHer\n<a href=\"http://liveworld.com/company/team.html#JW\">Jenna Woodul</a>, Executive Vice President and Chief Community Officer, LiveWorld</p>\n<p>Importance of community. Is community a business model or does it need to be bought to have value.</p>\n<p>Caterina Fake - Flickr was about break even when it was acquired. Big competitor was Photolog. Was started as a site for friends to share sites. They did not start as a business so it was hard for them to become a business. Flickr started as a business and as such they took care to design it as a business. Communities that are organic are owned by the community. Value is in the peripheral services.</p>\n<p>Lisa – The community is worth money.</p>\n<p>Jenna – Research of community vs single user. Studies showed ability to recal brands was much greater when engaged in a venue where users where interacting with each other – 4 times greater. Communities come 9 times longer. Stay 5 times longer. Buy more. Spend 35 – 57% more depending on study.</p>\n<p>What makes effective community?</p>\n<p>What is a community killer?\nCaterina Fake – inattention. There needs to be people in there stomping on the trolls. When they started flickr they stayed online and helped out and interacted with each user for about 100 days to build the culture. Decide what you want to permit. What is cool. They mold the commuity.</p>\n<p>Jenna Woodul – agree with Caterina. Brand your community, but let the message be driven by the people in the community</p>\n<p>Jessica Hardwick – Flexibility. Create tools that let some users control their interaction while allowing others to open up to anyone.</p>\n<p>Lisa Stone – don’t lie. Recent example with walmart blog. Walmart hired a couple of journalists who pretended to be a couple of middle age people traveling the country in their RV. They failed to tell users that the users were hired by walmart. Tell the truth.</p>\n<p>How do you grow your community?</p>\n<p>Caterina Fake – running ads all over the internet is a bad way to grow users. Users come via their friends that is the point of a social network. It helps if friends bring in friends. You need an “Abraham” a person or people who “begat” other users and shephard them along. You need key players.\nBefore flickr could setup credit cards they gave away pro accounts to users to give away which made their users look good. Caterina posted on her blog about tea. People made comments. She bought some tea and the company offered a $5 discount to anyone so she got the emails of all the people who posted and emailed them the discount and they loved her. She basically did the companies marketing for them.</p>\n<p>Lisa Stone - You have to be passionate about answering the users questions.</p>\n<p>Jessica Hardwick – wear shirts talk to everyone about the site.</p>\n<p>Monetizing a community</p>\n<p>Jory Des Jardins – turning a community into money has killed many communities.</p>\n<p>Lisa Stone – Asked everyone if they wanted to participate in an ad network. Those who did not want to participate did not have to.</p>\n<p>Jenna Woodul – Corporations are starting communities for many reasons – ads, research about customers, support using peer to peer conversation, others want buzz. When you hold a party you can’t just invite people in and leave them. You need leadership to meet and greet. Those individuals need to carry the culture forward. Trust increases when companies interact with users and allow them to interact even if the comments aren’t positive about the company.</p>\n<p>Jessica Hardwick – Trust within a community. You can’t have a swap transaction without trust that both parties will deliver. Anyone can setup a reputation system. Trust circles or swap circles – people know each other or people know people. Involve users – ask them questions. Users wanted to brand their community. They wanted public and private communities. Reputation is their single most important parameter. People under 18 not allowed. Feedback forms. When launched they built a repuation system. Users asked if they could pull reputation from ebay but they couldn’t. Didn’t like that they were building a closed reputation system. Partnered with rapleaf (<a href=\"http://www.rapleaf.com/\">http://www.rapleaf.com/</a>). They allow users to own their reputation and take it across systems.</p>\n<p>How do you get your first advocates?</p>\n<p>Caterina Fake – Empower users to own some part of your community. Communities tend to take on the characteristics of their founders so be sure to invite people of many backgrounds in. It can be hard to moderate communities when there are many cultures. Flickr had a large users base in Arab countries. In those countries certain pictures of women were not acceptable.</p>\n<p>How do you deal with scale?</p>\n<p>Jenna Woodul – you have to manage new and veteran users. Hopefully, your community leaders do that. In some communities allowing people to have designations based on time in the community is sometimes helpful depending on the culture.</p>\n<p>Tools\nCaterina Fake – build tools that let users expand what you do in ways that you may not currently understand. I hate pre canned community software. They lack identity and tend to be all the same.</p>\n<p>Jessica Hardwick – users kill the spam.</p>","frontmatter":{"title":"BlogHer Presents&#8230;World Domination via Collaboration","date":"November 07, 2006","imageUrl":null,"image":null}}},"pathContext":{"path":"/blogher-presentsworld-domination-via-collaboration"}}

/***/ })

